import React from 'react';
import styled from 'styled-components';
import { Innerlayout } from '../../styles/layout';
import PersonCard from '../util/PersonCard';
import Primarybutton from '../util/Primarybutton';
import { CommonStyles } from '../../staticFiles/CommonStyles';
import p1 from '../../assets/profile/person1.jpeg';
import p2 from '../../assets/profile/person2.png';
import p3 from '../../assets/profile/person3.jpg';
import p4 from '../../assets/profile/person4.jpeg';

const Ourtemadata = [
  {
    name: '',
    job: 'Creative Director',
    job2: 'CDO(Chief Design Officer)',
    image: p1,
  },
  {
    name: '',
    job: 'Marketing Director',
    job2: 'CMO(Chief Marketing Officer)',
    image: p4,
  },
  {
    name: '',
    job: 'Development Director',
    job2: 'CTO(Chief Technology Officer)',
    image: p2,
  },
  {
    name: '',
    job: 'Development Manager',
    job2: 'CFO(Chief Financial Officer)',
    image: p3,
  },
];

const Ourteam = () => {
  return (
    <Innerlayout>
      <Ourteamstyle>
        <div className="title">
          <h4 className="main">  </h4>
          <div className="line"></div>
          <p className="paragraph">
            <span className="onroom">ONROOM</span>    
                ''    
             , ,       
              ONROOM  . <br />
                ,    
                 
          </p>
        </div>
        <div className="cardcontainer">
          {Ourtemadata.map((data, index) => {
            return (
              <PersonCard
                key={index}
                name={data.name}
                job={data.job}
                image={data.image}
              />
            );
          })}
        </div>
      </Ourteamstyle>
    </Innerlayout>
  );
};

const Ourteamstyle = styled.section`
  margin-bottom: 100px;
  .cardcontainer {
    margin-top: 76.8px;
    padding: 2rem;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 1px;
    row-gap: 30px;
    @media screen and (max-width: 425px) {
      row-gap: 30px;
      grid-template-columns: repeat(1, 1fr);
      padding: 0;
      width: 100vw;
      column-gap: none;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    @media screen and (max-width: 375px) {
      row-gap: 30px;
      grid-template-columns: repeat(1, 1fr);
      padding: 0;
      width: 100vw;
      column-gap: none;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  .title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .line {
      background-color: ${CommonStyles.color.PrimaryLight2};
      width: 130px;
      height: 1px;
      border-radius: 30px;
      margin-top: 3px;
      margin-bottom: 16px;
      @media screen and (max-width: 425px) {
        margin-top: -13px;
      }
      @media screen and (max-width: 375px) {
        margin-top: -13px;
      }
    }
    .main {
      font-weight: bold;
      font-size: 36px;
      line-height: 52px;
      text-align: center;
      letter-spacing: -0.4px;
      color: ${CommonStyles.color.Primary};
      margin-bottom: 20px;
      @media screen and (max-width: 425px) {
        font-size: 2.5rem;
      }
      @media screen and (max-width: 375px) {
        font-size: 2rem;
      }
    }
    .paragraph {
      .onroom {
        color: ${CommonStyles.color.Primary};
        font-weight: bold;
      }
      font-family: DM Sans;
      font-weight: normal;
      font-size: 16x;
      line-height: 30px;
      /* or 145% */

      text-align: center;

      /* Colors / Text */
      width: 915px;
      color: #231f20;

      mix-blend-mode: normal;
      @media screen and (max-width: 425px) {
        width: 60vw;
        margin-bottom: 3rem;
      }
      @media screen and (max-width: 375px) {
        width: 70vw;
      }
    }
  }
`;

export default Ourteam;
