import React from 'react';
import { useRef } from 'react';
import styled from 'styled-components';
import { CommonStyles } from '../../staticFiles/CommonStyles';

const Why = styled.div`
  max-width: 1300px;
  margin: 120px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  .title {
    color: ${CommonStyles.color.Primary};
    font-weight: ${CommonStyles.bold.ExtraBold};
    font-family: DM Sans;
    font-size: 64px;
    text-align: 'center';
    @media screen and (max-width: 375px) {
      font-size: 2rem;
    }
    @media screen and (max-width: 425px) {
      font-size: 2.5rem;
    }
  }
  .line {
    width: ${({ lineWidth }) => `${lineWidth * 0.4}px`};
    height: 1px;
    background-color: ${CommonStyles.color.PrimaryLight2};
  }
  .firstCards {
    margin-top: 24px;
    display: flex;
    flex-direction: row;
    width: 70%;
    justify-content: space-between;
    align-items: space-between;
    @media screen and (max-width: 375px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    @media screen and (max-width: 425px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
  .card {
    width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h4 {
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 70px;
      color: ${CommonStyles.color.Primary};
      font-weight: ${CommonStyles.bold.ExtraBold};
    }
    p {
      width: 400px;
      text-align: center;
      font-weight: ${CommonStyles.bold.Medium};
      @media screen and (max-width: 375px) {
        width: 240px;
      }
      @media screen and (max-width: 425px) {
        width: 290px;
      }
    }
  }
`;

const WhyonRoom = () => {
  const TitleRef = useRef();

  return (
    <Why lineWidth={TitleRef?.current?.clientWidth}>
      <div ref={TitleRef} className="title">
        WHY ONROOM?
      </div>
      <div className="line" />
      <section className="firstCards">
        <div className="card">
          <h4>
            01.<br></br>
          </h4>

          <p>
                     
             .
          </p>
        </div>
        <div className="card">
          <h4>
            02.<br></br> 
          </h4>
          <p>
                  
            .
          </p>
        </div>
      </section>

      <section className="firstCards">
        <div className="card">
          <h4>
            03.<br></br>
          </h4>
          <p>
                   
                  .
          </p>
        </div>
        <div className="card">
          <h4>
            04.<br></br>
          </h4>
          <p>
                  
               .
          </p>
        </div>
      </section>
    </Why>
  );
};

export default WhyonRoom;
